@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w:hover {
    font-weight: 600;
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 2px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.info-block__header {
  margin-bottom: 15.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 40px;
}
@media screen and (max-width: 960px) {
  .info-block__header {
    margin-bottom: 56px;
    column-gap: 30px;
  }
}
@media screen and (max-width: 690px) {
  .info-block__header {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media screen and (max-width: 490px) {
  .info-block__header {
    column-gap: 10px;
  }
}
.info-block__header .info-block__title {
  text-align: center;
  grid-column: 1/3;
  max-width: 130.5rem;
  margin: 0 auto 70px;
}
@media screen and (max-width: 690px) {
  .info-block__header .info-block__title {
    margin: 0 auto 24px;
    grid-column: 1/2;
  }
}
.info-block__text {
  line-height: 125%; /* 20px/16 */
  letter-spacing: 0%;
}
.info-block__body .info-block__title {
  text-align: center;
  grid-column: 1/3;
  max-width: 130.5rem;
  margin: 0 auto 70px;
}
@media screen and (max-width: 960px) {
  .info-block__body .info-block__title {
    margin: 0 auto 24px;
    grid-column: 1/2;
  }
}
.info-block__content {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 40px;
  row-gap: 70px;
}
@media screen and (max-width: 1440px) {
  .info-block__content {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 960px) {
  .info-block__content {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 690px) {
  .info-block__content {
    grid-template-columns: repeat(1, 1fr);
    row-gap: 20px;
  }
}

.info__column {
  border-top: 1px solid #a9a9a9;
  padding-top: 10px;
}
.info__title {
  margin-bottom: 30px;
}
@media screen and (max-width: 690px) {
  .info__title {
    margin-bottom: 10px;
  }
}
.info__text {
  line-height: 125%; /* 20px/16 */
}